  export const create = (address, provider) => {
    const contract = new Contract(address, abi, provider);

    // READ
    const balanceOf = (address) =>
      contract.balanceOf(address).then((val) => parseFloat(ethers.utils.formatEther(val)));
  
    const allowance = (owner, spender) =>
      contract.allowance(owner, spender).then((val) => parseFloat(ethers.utils.formatEther(val)));

    const decimals = () => contract.decimals().then((val) => val);
  
    // WRITE
    const approve = (spender, value) => contract.approve(spender, value);
  
    return { balanceOf, allowance, approve, decimals };
  }